import { useState, useEffect } from "react";
import { useLazyQuery } from "@apollo/client";
import gql from "graphql-tag";
import "./App.css";
import { useLocation } from "react-router";
import GoogleMapReact from "google-map-react";

const PointerComponent = () => (
  <img
    src="https://cdn3.iconfinder.com/data/icons/maps-and-navigation-7/65/68-512.png"
    width="48px"
    height="48px"
  />
);

const googleMapApiKey = "AIzaSyAyV_1HJX0RUxNU_5O03yVDUy_ar1KBI2Y";

function App() {
  const [mapInfoDetails, setMapInfoDetails] = useState(null);
  const { pathname } = useLocation();
  const [markerCenter, setMarketCenter] = useState({ lat: 0, lng: 0 });

  const mrgB0 = {
    marginBottom: 0,
  };

  const infoBoxStyle = {
    minWidth: "200px",
    position: "absolute",
    left: "10px",
    bottom: "30px",
    zIndex: "9999999",
    padding: "1rem",
    backgroundColor: "rgb(255, 255, 255)",
    boxShadow: "rgb(0 0 0 / 15%) 0px 0.5rem 1rem",
    borderRadius: "0.25rem",
    boxSizing: "border-box",
    textTransform: "capitalize",
  };

  const boxStyleHead = {
    marginTop: 0,
  };

  const [getData, { loading, error, data }] = useLazyQuery(
    gql`
      query getUser($id: ObjectId!) {
        user(query: { _id: $id }) {
          _id
          firstName
          lastName
          location
        }
      }
    `,
    {
      pollInterval: 1000,
    }
  );

  useEffect(() => {
    if (data) {
      const { user } = data;
      const center = {
        lat: user.location[0] || 0,
        lng: user.location[1] || 0,
      };
      setMapInfoDetails(data);
      setMarketCenter(center);
    }
  }, [data]);

  useEffect(() => {
    const id = pathname.split("/").pop();
    if (id) {
      getData({ variables: { id } });
    }
  }, [pathname]);

  if (loading) {
    return (
      <div className="wrap-loader">
        <div className="loader">
          <div className="box"></div>
          <div className="box"></div>
          <div className="box"></div>
          <div className="box"></div>
          <div className="wrap-text">
            <div className="text">
              <span>L</span>
              <span>O</span>
              <span>A</span>
              <span>D</span>
              <span>I</span>
              <span>N</span>
              <span>G</span>
              <span>...</span>
            </div>
          </div>
        </div>
        <div className="loader-text">wait please</div>
      </div>
    );
  }
  if (error) {
    return <div>Encountered an error: {error}</div>;
  }


  return (
    <>
      <div style={infoBoxStyle}>
        <h4 style={boxStyleHead}>
          Name:{" "}
          {mapInfoDetails
            ? `${mapInfoDetails.user.firstName} ${mapInfoDetails.user.lastName}`
            : null}
        </h4>
        <p>
          Location:{" "}
          {mapInfoDetails
            ? `Lat(${mapInfoDetails.user.location[0]}) Long(${mapInfoDetails.user.location[1]})`
            : null}
        </p>
        <p style={mrgB0}>
          Id: {mapInfoDetails ? mapInfoDetails.user._id : null}
        </p>
      </div>

      <div style={{ width: "100vw", height: "100vh" }}>
        <GoogleMapReact
          bootstrapURLKeys={{ key: googleMapApiKey }}
          defaultCenter={markerCenter}
          defaultZoom={20}
        >
          <PointerComponent lat={markerCenter.lat} lng={markerCenter.lng} />
        </GoogleMapReact>
      </div>
    </>
  );
}

export default App;
